.products {
  margin: 20px auto;
  width: 80%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  /* background-color: rgb(2, 170, 170); */
  background-color: pink;
  border-radius: 5px;
}

.card {
  width: 300px;
  height: 350px;
  border: 1px solid black;
  border-radius: 5px;
  overflow: hidden;
  padding: 10px;
  margin: 40px 20px;
  background-color: white;
}

.immg {
  width: 300px;
  height: 200px;
}

.product-image {
  width: 100%;
  height: 100%;
  display: block;
  
}

.product-name {
  text-transform: uppercase;
  margin: 10px 0;
}

.product-price {
  margin: 10px 0;
  padding-bottom: 20px;
  font-size: 18px;
  line-height: 30px;
}

.product-add-button {
  font-weight: 700;
  font-size: 18px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: black;
  color: white;
  width: 100%;
  height: 40px;
  display: block;
  cursor: pointer;
  text-transform: uppercase;
  margin: 10px auto;
  margin-bottom: 20px;
}

.product-add-button:hover{
    background-color:red;
}

.product-add-button:active{
    transform: translateY(2px);
}