.cart-items{
    margin: 30px auto;
    width: 50%;
    background-color: pink;
    border: 2px solid rgb(230, 84, 108);
    border-radius: 5px;
}

.cart-items-header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
    height: 50px;
    border-bottom: 2px solid black;
}

.cart-items-empty{
    display: flex;
    justify-content: center;
    align-items:center;
    font-size: 18px;
    font-weight: 700;
    padding-top: 20px;
    padding-bottom: 20px;
}

.cart-items-image{
    width: 115px;
    height: 115px;
    border: 2px solid black;
    margin-right: 10px;
}

.cart-items-list{
    display: flex;
    align-items:center;
    padding-top: 20px;
    padding-left: 20px;
    padding-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
    margin-left: 5%;
}

.cart-items-name,
.cart-items-function{
    width: 25%;
}

.cart-items-add,
.cart-items-remove{
    width: 30px;
    margin-right: 15px;
    font-weight: 700;
    border:none;
    border-radius:3px;
    font-size: 18px;
    color: white;
    cursor: pointer;
}

.cart-items-add{
    background-color:red;
}

.cart-items-remove{
    background-color:green;
}

.cart-items-add:active{
    transform: translateY(1.2px);
}

.cart-items-remove:active{
    transform: translateX(1.2px);
}

.cart-items-total-price-name{
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 22%;
    font-size: 20px;
    font-weight: 700;
    border-top: 2px solid black;
}

.cart-items-total-price{
    display: flex;
    padding-left: 45%;
}

.clear-cart{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
    margin-right: 10px;
}

.clear-cart-button{
    border:none;
    border-radius:5px;
    background-color:#bd0e0e;
    height: 30px;
    width: 100px;
    font-size: 18px;
    color:white;
    cursor: pointer;
}

.clear-cart-button:hover{
    background-color:#0e5557;
}

.cart-length{
    margin: 3px;
    font-size: 14px;
    font-weight: 700;
    vertical-align: super;
    color: black;
}

.cart{
    padding-right: 20px;
    margin-right: 30px;
}